import locationData from 'common/locationData.js';

export default {
	data() {
		return locationData;
	},
	methods: {
		updateCity: function() {
			for (var i in this.arr) {
				var obj = this.arr[i];
				if (obj.name == this.province) {
					this.cityArr = obj.sub;
					break;
				}
			}
			this.city = this.cityArr[1].name;
		},
		updateDistrict: function() {
			for (var i in this.cityArr) {
				var obj = this.cityArr[i];
				if (obj.name == this.city) {
					this.districtArr = obj.sub;
					break;
				}
			}
			if (this.districtArr && this.districtArr.length > 0 && this.districtArr[1].name) {
				this.district = this.districtArr[1].name;
			} else {
				this.district = '';
			}
		},
		gotoFruit: function() {
			uni.navigateTo({
				url: '/pages/crowdingOrdering/fruit'
			});
		},
		gotoSupermarket:function() {
			uni.navigateTo({
				url: '/pages/crowdingOrdering/supermarket'
			});
		},
		gotoMedicine:function() {
			uni.navigateTo({
				url: '/pages/crowdingOrdering/medicine'
			});
		}
	},

	mounted: function() {
		this.updateCity();
		this.updateDistrict();
	},

	watch: {
		province: function() {
			this.updateCity();
			this.updateDistrict();
		},
		city: function() {
			this.updateDistrict();
		},
		district: function() {}
	}
};
